@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,600;0,900;1,900&family=Roboto+Slab:wght@400;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,600;0,900;1,900&display=swap);


.navbar {
  position: absolute;
  top: 0;
  left: 0;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;
  height: 89px;
  background-color: white;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.813rem;
  letter-spacing: 1.9px;
}

.website-logo-name {
  margin: 15px 0;
  color: #0290ff;
  font-size: 1.875rem;
  font-weight: bold;
  letter-spacing: 1px;
}

.navbar > .left-side {
  width: inherit;
  list-style-type: none;
  padding: 0;
  display: flex;
  align-items: center;
  margin-left: 8%;
}

.left-side > ul {
  list-style-type: none;
  display: flex;
  margin: 0;
}

.left-side > ul > li {
  margin: 0 0 0 50px;
}

.right-side {
  margin: 7px 8% 0 0;
}

.right-side > img {
  width: 27px;
  height: 27px;
}

.links {
  text-decoration: none;
  color: #121212;
  opacity: 0.5;
}

.activelinks {
  opacity: 100%;
}

.d-flex {
  display: flex;
}

.book-container {
  font-family: 'Roboto Slab', serif;
  justify-content: space-between;
  align-items: center;
  border-top: solid 1px rgb(214, 214, 214);
  margin-bottom: 5px;
  padding: 10px 5%;
}

.general-info {
  flex-flow: column wrap;
  width: 30%;
}

.title {
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: -0.2px;
  color: #121212;
  margin: 0;
}

.book-management {
  list-style-type: none;
  padding: 0;
}

.current-chapter > button {
  width: 10rem;
  height: 2.063rem;
  margin-top: 1.6rem;
  padding: 0.438rem 1.188rem 0.5rem 1.375rem;
  background-color: #0290ff;
  color: azure;
  border: none;
  border-radius: 3px;
  font-size: 0.813rem;
  letter-spacing: 0.5px;
  cursor: pointer;
}

.book-management > li > button {
  margin: 0 15px 0 0;
  border: none;
  cursor: pointer;
  padding: 0;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
}

.current-chapter > button:hover {
  background-color: #036cbd;
}

.book-management > li > button:hover {
  color: #004886;
}

.completion-detail {
  align-items: center;
}

.progress-bar {
  width: 80px;
  height: 80px;
  margin-right: 10px;
}

.completion-text {
  flex-direction: column;
}

.completed-word {
  opacity: 0.7;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  color: #121212;
}

.current-chapter {
  flex-direction: column;
}

.genre {
  color: #121212;
  opacity: 0.5;
  font-size: 0.875rem;
  font-weight: bold;
}

.author {
  font-size: 0.875rem;
  font-weight: 300;
  letter-spacing: normal;
  color: #4386bf;
}

.current-chapter-span {
  font-size: 0.813rem;
  font-weight: 300;
  color: #121212;
  opacity: 0.5;
  margin-bottom: 0.6rem;
}

.current-chapter-value {
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: -0.4px;
  color: #121212;
}

main {
  position: absolute;
  width: 100%;
  z-index: -1;
  top: 115px;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-container {
  width: 90%;
  display: flex;
  flex-direction: column;
}

.book-list-container {
  margin: 1rem 5%;
}

form {
  margin-top: 20px;
  border-top: 1px solid rgb(214, 214, 214);
  padding: 10px 0 50px 0;
}

.adding-form {
  height: 2.5rem;
  justify-content: space-between;
  align-items: center;
}

.adding-span {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;
  font-weight: 800;
  letter-spacing: -0.18px;
  color: #888;
}

.book-input {
  font-family: 'Montserrat', sans-serif;
  width: 30%;
  height: 1.75rem;
  border-radius: 4px;
  padding: 1.8px 1%;
  font-weight: 600;
  color: black;
  border: solid 1px rgba(148, 148, 148, 0.801);
  letter-spacing: -0.15px;
}

.book-input:focus {
  outline-color: rgb(88, 88, 88);
}

.selector-input {
  font-family: 'Montserrat', sans-serif;
  height: 2.1rem;
  border-radius: 4px;
  background-color: #fff;
  border: solid 1px rgba(148, 148, 148, 0.801);
  color: rgb(65, 65, 65);
}

.selector-input > option:not(:disabled) {
  color: #000;
  font-weight: 600;
}

.adding-form > button {
  width: 11.5rem;
  height: 2.1rem;
  cursor: pointer;
  color: azure;
  border: none;
  border-radius: 3px;
  background-color: #0290ff;
  font-family: 'RobotoSlab', sans-serif;
  font-size: 0.813rem;
  letter-spacing: 0.5px;
  text-align: center;
}

.adding-form > button:hover {
  background-color: #036cbd;
}

